<template>
    <el-dialog title="" :visible.sync="open" width="900px" append-to-body>
      <el-form label-width="80px">
        <el-row>
          <el-col :span="10">
            <el-form-item label="">
              <el-input type="text" id="searchAddress" v-model="searchAddress" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="14">
            <el-form-item label="">
              <el-input v-model="addressInfo.address" placeholder="">
                <template slot="prepend">
                  {{addressInfo.province}}{{addressInfo.city}}{{addressInfo.district}}
                </template>
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <!--  -->
      <div id="map-container" style="width: 100%; height: 400px;"></div>
  
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm"></el-button>
        <el-button @click="cancel"></el-button>
      </div>
    </el-dialog>
  </template>
  
  <script>
    export default {
        name:"BMapAddressSelect",
      data() {
        return {
          // 
          searchAddress: "",
          // 
          addressInfo: {
            // 
            longitude: "",
            // 
            latitude: "",
            // 
            province: "",
            // 
            city: "",
            // 
            district: "",
            // 
            address: ""
          },
          open: false,
        }
      },
      methods: {
        // 
        initBaiduMap() {
          let that = this;
          this.$nextTick(function () {
            /*  start */
            var map = new BMap.Map("map-container") // 
            var point = new BMap.Point(113.410789, 23.118483); // 
            map.centerAndZoom(point, 13); // 
            map.enableScrollWheelZoom(true); //
            var marker = new BMap.Marker(point); // 
            map.addOverlay(marker); // 
            /*  end */
  
            /** Start */
            // 
            map.addEventListener("click", function (e) {
              var click = e.point; // 
              map.clearOverlays(); // 
              var marker = new BMap.Marker(click); // 
              map.addOverlay(marker); // 
  
            })
            /** End */
  
            /** Start */
            // 
            var ac = new BMap.Autocomplete({
                input: "searchAddress",
                location: map,
              });
            // 
            ac.addEventListener("onconfirm", function (e) {
              map.clearOverlays();
              var local = new BMap.LocalSearch(map, {
                // 
                onSearchComplete: function (res) {
                  let poi = res.getPoi(0); // 
                  var searchpt = poi.point; // 
                  map.centerAndZoom(searchpt, 16);
                  map.addOverlay(new BMap.Marker(searchpt));
                  that.geocAddress(searchpt);
                }
              });
              // 
              var searchValue = e.item.value;
              local.search(
                searchValue.province +
                searchValue.city +
                searchValue.district +
                searchValue.street +
                searchValue.business
              )
            });
            /** End */
          })
        },
        // 
        geocAddress(point) {
          let that = this;
          var geoc = new BMap.Geocoder();
          geoc.getLocation(point, function (geocInfo) {
            // 
            var addressInfo = geocInfo.addressComponents;
            that.addressInfo.longitude = point.lng;
            that.addressInfo.latitude = point.lat;
            that.addressInfo.province = addressInfo.province;
            that.addressInfo.city = addressInfo.city;
            that.addressInfo.district = addressInfo.district;
            let address = addressInfo.street + addressInfo.streetNumber;
            if (geocInfo.surroundingPois.length > 0) {
              address = address + geocInfo.surroundingPois[0].title;
            }
            that.addressInfo.address = address;
          });
        },
        /**  */
        show(){
          this.open = true;
          this.initBaiduMap();
        },
        /**  */
        confirm() {
          this.$emit("confirmMapAddress", this.addressInfo);
          this.open = false;
        },
        cancel() {
          this.open = false;
        }
      },
    }
  </script>
  <style scoped>
    
    .tangram-suggestion {
      z-index: 9999;
     }
  </style>
  
  
